<template>
  <v-container>
    <v-card>
      <v-card-title>
        {{ $t('Help') }}
      </v-card-title>
      <v-card-text>
        <v-tabs vertical>
          <v-tab v-for="(content, tab) in data" :key="tab">
            {{ tab }}
          </v-tab>
          <v-tab-item v-for="(content, tab) in data" :key="tab">
            <v-card flat>
              <v-card-text v-html="content" />
            </v-card>
          </v-tab-item>
        </v-tabs>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
import axios from 'axios'

export default {
  metaInfo () {
    return { title: this.$t('Help') }
  },

  data () {
    return {
      data: {}
    }
  },

  async created () {
    const { data } = await axios.get('/api/admin/help')
    this.data = data
  }
}
</script>
